












































































import Vue from 'vue'
import axios from 'axios'
import { Prop } from 'vue-property-decorator'
import Component from 'vue-class-component'

@Component
export default class EditInstanceConn extends Vue {
  @Prop({ default: null })
  instance: any

  show: boolean = true
  error: boolean = false
  loading: boolean = false
  form: any = {
    host: '',
    port: null,
    identity: '',
    cid: null,
    name: 'My Yamba Instance',
    nick: 'Yamba-Bot',
    autostart: true
  }

  loadData (): void {
    this.$emit('loadingChange', true)
    axios
      .get('api/instances/' + this.instance + '/core')
      .then(response => {
        this.form = response.data
      })
      .catch(error => {
        console.log(error)
        this.error = true
      })
      .finally(() => (this.$emit('loadingChange', false)))
  }

  mounted () {
    if (this.instance != null) {
      this.loadData()
    }
  }

  onSubmit (evt: Event): void {
    evt.preventDefault()
    console.log('onSubmit')

    console.log(this.$data)
    this.$emit('onSubmit', this.form)
  }

  onReset (evt: Event): void {
    evt.preventDefault()
    console.log('onReset')
    if (this.instance !== null) {
      this.loadData()
    } else {
      this.form.host = ''
      this.form.identity = ''
      this.form.cid = null
      this.form.nick = 'Yamba-Bot'
      this.form.autostart = false
      this.form.port = null
      // Trick to reset/clear native browser form validation state
      this.show = false
      this.$nextTick(() => {
        this.show = true
      })
    }
  }
}
