










































import axios from 'axios'
import Vue from 'vue'
import MinInstance from '@/types.ts'
import { Component, Prop } from 'vue-property-decorator'
import DeleteModal from '@/components/DeleteModal.vue' // @ is an alias to /src

@Component({
  components: {
    DeleteModal
  }
})
export default class Home extends Vue {
  $refs: {
    // eslint-disable-next-line
    del_modal: DeleteModal
  }
  instances: MinInstance[] = []
  error: boolean = false
  errorMsg: String = 'No details.'
  loading: boolean = false

  public createInstance (): void {
    this.$router.push({ name: 'createInstance' })
  }

  deleteInstance (instance: MinInstance): void {
    console.log(instance)
    this.$refs.del_modal.show(instance.name, instance.id)
  }

  deleteConfirmed (instance: Number): void {
    console.log('confirmed ' + instance)
    axios
      .delete('api/instances/' + instance)
      .then(response => {
        console.log('success deleting ' + instance)
        this.instances = this.instances.filter(x => x.id !== instance)
      })
      .catch(error => {
        console.log(error)
        this.error = true
      })
      .finally(() => (this.loading = false))
  }

  changeInstance (instance: Number): void {
    this.instances.forEach((elem: any) => {
      if (elem.id === instance) {
        let action = 'start'
        if (elem.running) {
          action = 'stop'
        }
        axios
          .post('api/instances/' + elem.id + '/' + action)
          .then(response => {
            console.log('success changing ' + elem.id)
            elem.running = !elem.running
          })
          .catch(error => {
            console.log(error)
            this.error = true
          })
          .finally(() => (this.loading = false))
      }
    })
  }

  private stopInstance (elem: MinInstance): void {
    axios
      .post('api/instances/' + elem.id + '/stop')
      .then(response => {
        console.log('success stopping ' + elem.id)
        elem.running = false
      })
      .catch(error => {
        console.log(error)
        this.error = true
      })
      .finally(() => (this.loading = false))
  }

  public editInstance (instance: Number): void {
    console.log('edit ' + instance)
    this.$router.push({ name: 'editInstance', params: { id: '' + instance } })
    // use implizit string casting as workaround
  }

  mounted () {
    this.loading = true
    axios
      .get('api/instances')
      .then(response => {
        this.instances = response.data
      })
      .catch(error => {
        console.log(error)
        this.error = true
      })
      .finally(() => (this.loading = false))
  }
}
