







import Vue from 'vue'
import axios from 'axios'
import { Component, Prop } from 'vue-property-decorator'
import EditInstanceConn from '@/components/EditInstanceConn.vue'

@Component({
  components: {
    EditInstanceConn
  }
})
export default class Home extends Vue {
  error: boolean = false
  loading: boolean = false
  instance: Number

  toggleLoading (loading: boolean): void {
    this.loading = loading
  }

  mounted () {
    this.instance = Number(this.$route.params.id)
  }

  updateInstance (form: any): void {
    axios
      .put('api/instances/' + this.instance + '/core',
        form
      )
      .then(response => {
        console.log('updated')
        this.$router.push({ name: 'instances' })
      })
      .catch(error => {
        console.log(error)
        this.error = true
        this.loading = false
      })
      .finally(() => (this.loading = false))
  }
}
