








import Vue from 'vue'
import axios from 'axios'
import { Component, Prop } from 'vue-property-decorator'
import EditInstanceConn from '@/components/EditInstanceConn.vue'

@Component({
  components: {
    EditInstanceConn
  }
})
export default class CreateInstance extends Vue {
  loading: boolean = false
  error: boolean = false

  toggleLoading (loading: boolean): void {
    this.loading = loading
  }

  createInstance (form: any): void {
    this.loading = true
    console.log(form)
    axios
      .post('api/instances/create',
        form
      )
      .then(response => {
        if (form.autostart) {
          let instance = response.data
          axios.post('api/instances/' + instance + '/start')
            .then(response => {
              this.$router.push({ name: 'instances' })
            })
            .catch(error => {
              console.log(error)
              this.error = true
            })
            .finally(() => (this.loading = false))
        } else {
          this.$router.push({ name: 'instances' })
        }
      })
      .catch(error => {
        console.log(error)
        this.error = true
        this.loading = false
      })
  }
}
